.tooltip {
    display: inline-block;
    padding: .5rem;
    border-radius: 5px;
    box-shadow: silver 0 0 2px 1px;
    position: absolute;
    top: 0;
    left: 0;
    transition: 500ms;
    background-color: var(--background-color);
}
.tooltip-container svg {
    z-index: 2;
}

.tooltip h1 {
    padding: 0;
    margin: 0;
}

.tooltip-values {

    display: -webkit-flex;

    display: flex;
    text-align: left;
}

.tooltip-values > div {
    margin: 0 .5em;
    font-weight: bold;
    font-size: 130%;
}

.tooltip small {
    display: block;
    font-weight: lighter;
}

.timeline-view {
    -webkit-flex: 0 0 100px;
            flex: 0 0 100px;
    max-height: 100px;
    position: relative;
    width: 100%;
    /*background-color: blue;*/
    /*display: flex;*/
    margin: 5px 0;
}

.timeline-view svg {
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.timeline-view .bounds {
    display: -webkit-flex;
    display: flex;
    width: 100%;
}
.timeline-view input {
    outline: none;
border: none;
    border-image: none;

    margin: 0px;
}

